<template>
    <div id="all">
      <AdminNav></AdminNav>


      <div style="width: 800px;padding-left: 350px;padding-top: 30px">
<!--  博客标题-->
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="demo-ruleForm">
        <el-form-item prop="blogTitle">
          <el-input placeholder="请输入博客标题..." v-model="ruleForm.blogTitle">
            <template slot="prepend">标题</template>
          </el-input>
        </el-form-item>
<!--     markdown文本编辑器-->
        <el-form-item prop="editorContent">
          <div id="main" style="padding-top: 20px">
            <mavon-editor
              v-model="ruleForm.blogContent"
              ref="md"
              @change="change"
              :ishljs = "true"
              @imgAdd="editorUpdataImgs"
              style="height: 500px"/>
          </div>
        </el-form-item>

<!--        博客分类选择器-->
        <el-form-item prop="blogType">
          <el-select v-model="ruleForm.sortId" placeholder="分类"  style="padding-left: 15px">
            <el-option
              v-for="item in options"
              :key="item.sortId"
              :label="item.sortName"
              :value="item.sortId">
            </el-option>
          </el-select>
<!--    博客日期选择器-->
        </el-form-item>
        <el-form-item prop="blogDate" label="上传的日期：">
          <el-date-picker
            v-model="ruleForm.blogDate"
            type="date"
            placeholder="选择日期"
            >
          </el-date-picker>
        </el-form-item>
<!--    博客首图地址-->
        <el-form-item prop="blogPhoto" label="首图：">
<!--          <el-input placeholder="请输入首图引用地址..." v-model="ruleForm.blogPhoto">-->
<!--            <template slot="prepend" >首图</template>-->
<!--          </el-input>-->
          <el-upload
            class="avatar-uploader"
            :headers="header"
            action="http://localhost:8282/fxfblog/user/blog/uploadfile/"
            :show-file-list="false"
            :on-success="handleAvatarSuccess"
            :before-upload="beforeAvatarUpload">
            <img v-if="ruleForm.blogPhoto" :src="ruleForm.blogPhoto" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon el-upload"></i>
          </el-upload>
        </el-form-item>
<!--    提交返回按钮-->
        <el-form-item style="text-align: center">
          <el-button type="warning" plain>返回</el-button>
          <el-button type="info" plain>保存</el-button>
          <el-button type="success" @click="submit('ruleForm')" plain>提交</el-button>
        </el-form-item>

      </el-form>
      </div>



    </div>
</template>

<script>
    import AdminNav from "./AdminNav";
    export default {
      name: "AddBlog",
      components: {AdminNav},
      data() {

        return {
          header:{
            token:sessionStorage.getItem("token"),
          },

          activeIndex: 'home',
          input: '',
          textarea: '',
          //用户名
          userName: sessionStorage.getItem("userName"),
          //头像
          circleUrl: sessionStorage.getItem("photo"),

          ruleForm: {
            userId:sessionStorage.getItem("userId"),
            //博客标题输入框
            blogTitle: '',
            //博客内容markdown输入的内容
            blogContent: '',
            //博客上传时间
            blogDate: '',
            //博客首图地址
            blogPhoto: '',
          },
          //博客分类选择器
          blogType: '',
          options: [{
            sortId: '',
            sortName: ''
          }],

          rules: {
            blogTitle: [
              {required: true, message: '请输入博客标题', trigger: 'blur'},
              {min: 3, max: 20, message: '长度在 3 到 20 个字符', trigger: 'blur'}
            ],
            blogContent: [
              {required: true, message: '请输入博客内容', trigger: 'blur'},
            ],

            blogDate: [
              {required: true, message: '请选择上传日期', trigger: 'change'},
            ],
            blogPhoto: [
              {required: true, message: '请选择图片', trigger: 'change'},
            ]
          },

          // //需要解析的markdown数据
          // value: '',
          // value1:''
        }
      },
      created() {
        //  获取本地存储的token，判断用户是否登录，若本地保存的userId为空，则跳转到login页面登录

        console.log(sessionStorage.getItem("token"));
        if (sessionStorage.getItem("userId") == null) {
          this.$router.push('login')
        }
        //  查询分类列表
        const _this = this;
        this.$axios
          .get("http://localhost:8282/fxfblog/sort/allSort")
          .then(function (response) {
            _this.options = response.data;
            console.log(response.data.list)
          })
        if (this.$route.query.blogId != null){
          this.$axios.get('http://localhost:8282/fxfblog/blog/findBlogById/'+this.$route.query.blogId).then(function (response) {
            console.log(response.data)
            _this.ruleForm = response.data
          })
        }
      },

      methods: {
        //添加博客

        handleSelect(key, keyPath) {
          console.log(key, keyPath);
          this.$router.push(key).catch(err => err);
        },
        editorUpdataImgs(filename, file) {
          //上传图片
          let formData = new FormData();
          console.log(formData)
          formData.append('file', file);
          var $vm = this
          this.$axios.post('http://localhost:8282/fxfblog/user/blog/uploadfile/', formData).then(function (response) {
            // if (response.body.message) {
            //   this.$refs.editor.$img2Url(filename, response.body.data);//更改图片内文章上传地址
            // } else {
            //
            // }

              // console.log(response.data.data.url)
            $vm.$refs.md.$img2Url(filename, response.data.data.url);//更改图片内文章上传地址

            console.log(response.data)
            console.log(response.data.data.url)
          }, function (response) {
            this.$message.error('抱歉!图片上传失败!请联系管理员');
          });
        },
        change(value, render) {        //value为编辑器中的实际内容，即markdown的内容，render为被解析成的html的内容
          this.html = render;
        },
        // 提交
        submit(formName) {                    //点击提交后既可以获取html内容，又可以获得markdown的内容，之后存入到服务端就可以了
          console.log(this.ruleForm)
          const _this = this;
          this.$refs[formName].validate((valid) => {
            if (valid && this.$route.query.blogId == null) {
              _this.$axios.post('http://localhost:8282/fxfblog/user/blog/insertBlog', this.ruleForm).then(function (response) {
                console.log(response.data)
                if (response.data == 'success') {
                  //重新获取表单数据
                  console.log(response.data);
                  _this.$message.success("新增成功")
                } else if (response.data = 'error') {
                  _this.$message.error('添加失败');
                } else if (response.data = 'null') {
                  _this.$message.error('请输入内容')
                }
              })
            }else if(this.$route.query.blogId != null){
              _this.$axios.put('http://localhost:8282/fxfblog/user/blog/updateBlog', this.ruleForm).then(function (response) {
                console.log(response.data)
                if (response.data == 'success') {
                  //重新获取表单数据
                  console.log(response.data);
                  _this.$message.success("修改成功成功")
                } else if (response.data = 'error') {
                  _this.$message.error('添加失败');
                }
              })
            } else {
              console.log('error submit!!');
              return false;
            }
          });


        },
        handleAvatarSuccess(res, file) {
           console.log(res)
          // console.log(file)
          this.ruleForm.blogPhoto = res.data.url;
          console.log(this.ruleForm.blogPhoto)
        },
        beforeAvatarUpload(file) {//文件上传之前调用做一些拦截限制
          const isJPG = file.type === 'image/jpeg';
          const isLt2M = file.size / 1024 / 1024 < 2;
          // console.log(file)
          if (!isJPG) {
            this.$message.error('上传头像图片只能是 JPG 格式!');
          }
          if (!isLt2M) {
            this.$message.error('上传头像图片大小不能超过 2MB!');
          }
          return isJPG && isLt2M;
        }
      }
    }

</script>

<style scoped>
  #all{
    width: 100%;
    height: 100%;
  }
  /*导航条样式*/
  .el-menu{
    padding-left: 100px;
  }
  .log{
    margin-right: 100px;
    font-size: 30px !important;
  }
  .el-button--text{
    float: right;
    margin-right: 150px;
  }
  .el-dropdown-link {
    cursor: pointer;
  }
  .el-icon-arrow-down {
    font-size: 12px;
  }
/*  上传图片*/
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }

</style>
