<template>
  <div>
    <el-row :gutter="12" class="loginUser">
      <el-col :span="8">
        <el-card shadow="always" style="width: 450px">
          <h2 style="text-align: center;color: coral">管理员登录</h2>
          <div style="margin: 20px;"></div>

          <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
            <el-form-item label="用户名：" prop="userName">
              <el-input placeholder="请输入用户名" v-model="ruleForm.userName"></el-input>
            </el-form-item>
            <el-form-item label="密码：" prop="userPassword">
              <el-input placeholder="请输入密码" v-model="ruleForm.userPassword" show-password></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="submitForm('ruleForm')">登录</el-button>
              <el-button @click="resetForm('ruleForm')">注册</el-button>
            </el-form-item>
          </el-form>
        </el-card>
      </el-col>
    </el-row>

  </div>
</template>

<script>
    export default {
      name: "Login",
      data() {
        return {
          ruleForm: {
            useName: '',
            userPassword: ''
          },
          rules: {
            userName: [
              { required: true, message: '请输入用户名', trigger: 'blur' },
              { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
            ],
            userPassword: [
              { required: true, message: '请输入您的密码', trigger: 'change' }
            ],

          }
        };
      },
      methods: {
        submitForm(formName) {
          const _this = this
          this.$refs[formName].validate((valid) => {
            if (valid) {
              _this.$axios.post("http://localhost:8282/fxfblog/user/login",_this.ruleForm).then(function (response) {
                console.log(response.data)
                if (response.data!=null){
                  _this.$alert(_this.ruleForm.userName+'登陆成功','消息', {
                    confirmButtonText: '确定',
                    callback: action => {
                      _this.$router.push('/admin')
                    }
                  })
                  sessionStorage.setItem("userId",response.data.userId);
                  sessionStorage.setItem("userName",response.data.userName);
                  sessionStorage.setItem("photo",response.data.photo);
                  sessionStorage.setItem("token",response.data.token);
                  console.log(sessionStorage.getItem("token"))
                }else if (response.data==null){
                  _this.$alert('您的用户名密码错误，请重新输入','消息', {
                    confirmButtonText: '确定',
                    callback: action => {
                      _this.$router.push('login')
                    }
                })
                }
              })

            } else {
              console.log('error submit!!');
              return false;
            }
          });
        },
        resetForm(formName) {
          this.$refs[formName].resetFields();
        }
      }

    }
</script>

<style scoped>
  .loginUser{
    position: center;
    left: 500px;
    top: 180px;

  }

</style>
